.root {
  .header-container {
    padding: 0;
    margin: 0;

    .header {
      display: flex;
      align-items: center;
      padding: 10px;

      .title {
        flex: 1;
      }
    }
  }

  .dropdown {
    margin-bottom: unset !important;
  }

  .overdue {
    background-color: rgba(255,255,0,0.7);
  }

  .content {
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow: hidden;

    .search {
      margin-bottom: 10px;
    }
  }
}
