.root {
  position: relative;

  .upload-container {
    text-align: end;
    position: absolute;
    transform: translateY(-32px);
    right: 0;
    margin-right: 5px;

    .label {
      text-decoration: underline;
      &:hover {
        text-decoration: underline;
      }
    }
  }

  .form {
    padding: 20px 10px 10px 10px;

    .inputs {
      margin-bottom: 10px;

      .input {
        padding-bottom: unset;
      }
    }

    .actions {
      display: flex;
      justify-content: flex-end;

      .save {
        margin-left: 10px;
      }
    }
  }
}
