@import 'styles/color.scss';

.root {
  display: flex;
  flex-direction: column;
  padding: 15px;

  .header {
    display: flex;
    flex-direction: column;

    .field-filter {
      display: table;

      .input {
        display: table-row;
        flex-direction: row;
        white-space: nowrap;

        span {
          display: table-cell;
          padding-right: 5px;
        }
      }
    }
    .content {
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      .filter {
        display: flex;
        flex-direction: row;
        .input {
          display: flex;
          flex-direction: row;
          width: 30%;
          margin-right: 5px;
          p {
            margin-right: 5px;
            margin-left: 5px;
          }
        }
        .submit {
          margin-bottom: 21px;
          margin-left: 15px;
          height: 40px;
        }
      }
    }
  }

  .body {
    .table {
      max-height: 60vh;
      th {
        padding: 5px;
        white-space: nowrap;
        .input {
          padding: 5px;
        }
      }
      .remarks {
        min-width: 350px;
      }
      .table-cell {
        white-space: pre;
      }

      .footer {
        display: flex;
        flex-direction: row;
        white-space: nowrap;
        justify-content: space-between;
        min-width: 70vw;
      }
    }
  }

  .table-row:nth-child(even) {
    background: #ccc;
  }
  .table-row:nth-child(odd) {
    background: #fff;
  }
  .table-row:hover {
    background: #ccf;
  }
}
