@import 'styles/color.scss';

.root {
  display: flex;
  flex-direction: column;
  padding: 20px;
  margin-bottom: 20px;

  .table {
    margin-bottom: 20px;
    flex: 1;
    td,
    th {
      &.specialization {
        word-break: break-word;
      }
    }
  }

  .table-header {
    display: flex;
    padding-bottom: 10px;
    .table-description {
      flex: 1;
      color: $color-1;
    }

    .add-link {
      font-weight: 500;
      color: $color-12;
    }
    .add-link:focus {
      outline: $color-3 2px solid;
      outline-style: auto;
      outline-offset: 3px;
      color: $color-3;
    }
  }

  .row {
    .align {
      &.center {
        text-align: center;
      }

      &.right {
        text-align: right;
      }
    }

    &.discrepancy {
      background-color: $color-19;
      .icon-container {
        font-size: 18px;
        height: 25px;
        width: 25px;
        margin-left: 5px;
        margin-bottom: auto;
        padding: 3.4px;
        border-radius: 50%;
        background-color: $color-19;
        color: red;
        // margin: auto;
        display: flex;
        cursor: pointer;
      }
    }
  }

  .table-total {
    display: flex;
    padding-top: 10px;
    width: 100%;
    .caption {
      flex: 1;
      display: flex;
      justify-content: flex-end;
      padding-right: 20px;

      font-weight: bold;
    }

    .amount {
      font-weight: bold;
    }
  }

  .groups {
    padding-top: 10px;
    padding-bottom: 10px;

    .input {
      flex: 1;
      padding: 10px;

      &:first-child {
        padding-left: unset;
      }

      &:last-child {
        padding-right: unset;
      }
    }

    &.buttons {
      justify-content: flex-end;

      button {
        margin-left: 10px;
      }
    }
  }
}

.table-grid {
  padding-top: 10px;
  padding-left: 10px;
  padding-bottom: 10px;
}

.message {
  padding: 10px !important;
  width: 100%;
  max-width: 400px;
}
