@import 'styles/color.scss';

.root {
  display: flex;
  width: 100%;
  flex-direction: row;
  align-items: center;

  $read-only: 'no';

  .input-wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;

    &.max-width {
      width: 100%;
    }

    &.auto-width {
      width: auto;
      // h6 {
      //   width: 50%;
      // }
    }

    div[class*='autocomplete-field'] {
      margin-bottom: unset;
    }
    div[class*='text-field'] {
      margin-bottom: unset;
    }

    &.discrepancy {
      div[class*='text-field'] {
        background-color: $color-19 !important;
      }
      div[class*='autocomplete-field'] {
        background-color: $color-19 !important;
      }
      div[class*='DropDown_select-container'] {
        background-color: $color-19 !important;
      }
      div[class*='DropDown_input'] {
        background-color: $color-19 !important;
      }
      h6[class*='read-only-value'] {
        background-color: $color-19 !important;
        $read-only: 'yes';
      }
    }
  }

  .discrepancy-icon {
    height: 100%;

    &.with-margin {
      @if $read-only == 'yes' {
        margin-bottom: 5px;
      } @else {
        margin-bottom: 50px;
      }
      align-self: flex-end;
    }

    .icon {
      display: block;
      font-size: 18px;
      height: 25px;
      width: 25px;
      margin-left: 5px;
      padding: 3.4px;
      border-radius: 50%;
      background-color: $color-19;
      color: red;
      display: flex;
      cursor: pointer;
    }
  }
}

.display-hidden {
  visibility: hidden;
}

.display-none {
  display: none;
}

.message {
  padding: 10px !important;
  width: 100%;
  max-width: 400px;
}
