@import 'styles/color.scss';

.root {
  display: flex;
  flex-direction: column;
  padding: 15px;

  .header {
    display: flex;
    flex-direction: column;

    .typo {
      color: #2e7d32;
      text-decoration: underline;
    }
    .disabled {
      color: rgba(0, 0, 0, 0.5);
    }

    .content {
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      .filter {
        display: flex;
        flex-direction: row;
        .input {
          display: flex;
          flex-direction: row;
          width: 30%;
          margin-right: 5px;
          p {
            margin-right: 5px;
            margin-left: 5px;
          }
        }
        .submit {
          margin-bottom: 21px;
          margin-left: 15px;
          height: 40px;
        }
      }
    }
  }

  .buttonWrapper {
    align-self: flex-start;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    margin: 10px 0;
  }

  .body {
    display: flex;
    flex-direction: column;

    .table {
      max-height: 80vh;
      th {
        padding: 5px;
        white-space: nowrap;
        .input {
          padding: 5px;
        }
        .label {
          box-sizing: border-box;
        }
        .hidden {
          visibility: hidden;
          max-width: 10px;
        }
      }
      .remarks {
        min-width: 350px;
      }
      .table-cell {
        white-space: pre;
      }

      .handling {
        padding: 2px;
        text-align: center;
        font-style: italic;
      }
    }
  }

  .table-row:nth-child(even) {
    background: #ccc;
  }
  .table-row:nth-child(odd) {
    background: #fff;
  }
  .table-row:hover {
    background: #ccf;
  }
}

.footer{
  width: 100%;
  padding: 20px 20px 0 0;
  .actions{
    display: flex;
    justify-content: flex-end;

    button {
      margin-left: 10px;
    }
  }
}
