.root{
    .button-parent {
        display: flex;
        flex-direction: row;
        padding-top: 10px;
        justify-content: flex-start;
        
        button {
            margin-left: 10px;
            width: 250px;
            height: 30px;
            font-weight: bolder;
            font-size: 9pt;
        }

        button.active {
            background-color: #214930 !important;
        }
    }
    .header {
        margin-top: 20px;
        background-color: #b6b9b8;
        border-radius: 5px;
        .header-wrapper {
            padding: 20px;
            display: flex;
            button{
                width: 125px;
                margin-right: 50px;
            }
            .header-text{
                color: white;
                font-size: x-large;
                font-weight: bolder;
                flex-grow: 10;
            }
        }
    }
    .table {
        margin-top: 00px;
    }
    .add-item-root {
        margin-top: 20px;
        background-color: #b6b9b8;
        border-radius: 5px;
        .wrapper{
            padding: 20px;
            display: flex;
            .header-text{
                flex-grow: 1;
                color: white;
                font-size: large;
                font-weight: bolder;
                padding-top: 10px;
            }
            .text-wrapper{
                flex-grow: 5;
                width: 300px;
                margin-left: 10px;
                margin-right: 20px;
            }
            .radio-buttons{
                flex-grow: 1;
                margin-left: 40px;
                display: flex;
                color: white;
                font-size: large;
                font-weight: bolder;
            }
            .button-wrapper{
                flex-grow: 1;
                button{
                    margin-left: 10px;
                    width: 125px;
                }
            }

        }

        .button-wrapper-kpi {
            text-align: right;
            margin: 350px 40px 0px 0px;
            padding-bottom: 40px;
            button{
                margin-left: 10px;
                width: 125px;
            }
        }

    }
}