@import 'styles/color.scss';

.root {
  .header-container {
    padding: 0;
    margin: 0;

    .header1 {
      display: flex;
      flex-direction: row;
      align-items: center;
      padding: 10px;
      .title {
        flex: 1;
      }
    }
  }

  .body {
    padding-bottom: 30px;

    .content {
      display: flex;
      flex-direction: column;
      margin-bottom: 10px;

      .table {
        flex: 1;

        .selected {
          background-color: darken($color-10, 10);
        }
      }

      .groups {
        flex: 1;
        display: flex;
        flex-direction: row;

        .partition {
          width: 50%;
          border: 10px black;
          margin: 10px;

          p {
            margin-bottom: 10px;
          }
        }
      }
    }

    .action {
      flex: 1;
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      align-items: center;

      button {
        margin-right: 10px;
      }
    }
  }
}
