@import 'styles/color.scss';

.root {
  .header-container {
    padding: 0;
    margin: 0;

    .header {
      display: flex;
      align-items: center;
      padding: 10px;

      .title {
        flex: 1;
      }
    }
  }

  .search-container {
    padding: 5px;
    // padding: 40px 0 10px 0 ;
    display: flex;

    .reassign {
      align-items: center;
      display: flex;
      //margin-right: 5px;
    }

    .search {
      display: inline-block;
      margin-left: 10px;
      width: 1800px;
      //  min-width: 600px;
    }
  }

  .table {
    padding: 10px;

    .submitted {
      background-color: lighten($color-3, 17) ;
    }
  }

  .buttonWrapper {
    margin-top: 5px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
  }
}
