@import 'styles/color.scss';

.loading {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
}

.overlay {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: black;
  opacity: 70%;
  align-items: center;
  margin: auto;
  position: absolute;
  z-index: 999;
}
