@import 'styles/color.scss';

.root {
  flex: 1;
  display: flex;
  flex-direction: column;

  .toolbar {
    display: flex;
    flex-direction: row;

    button {
      margin-bottom: 10px;
      margin-right: 10px;
    }
  }

  .content {
    flex: 1;
    display: flex;
    flex-direction: column;

    .wrapper {
      flex: 1;
      display: flex;
      flex-direction: column;
      overflow-y: auto;
    }
  }
}
