.root {
  width: 100%;

  .title {
    text-align: center;
  }

  .container {
    display: flex;
    justify-content: center;

    .chart {
      max-width: 800px;
      flex: 1;
      display: flex;
      flex-direction: column;
      border-radius: 10px;
    }
  }

  .upper,
  .lower {
    display: flex;
  }
}
