@import 'styles/color.scss';

.root {
  display: flex;
  flex-direction: row;
  padding-top: 10px;

  button {
    margin-left: 10px;
  }

  .wrapper {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;

    .left {
      flex: 1;
      display: flex;
      align-items: center;
      flex-direction: row;
      justify-content: flex-start;
      column-gap: 8px;

      button {
        flex: 1;
        margin-left: unset;
      }

      .users {
        flex: 5;
        width: 100%;
        display: flex;
        align-items: center;

        .label {
          flex: 1;
          flex-direction: row;

          div > h6 {
            padding: 0 0 0 5px !important;
          }
        }
      }
    }

    .center {
      // flex: 1;
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
    }

    .right {
      flex: 1;
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
    }
  }
}

.escalate {
  .header-container {
    padding: 0;
    margin: 0;

    .header {
      display: flex;
      align-items: center;
      padding: 10px;

      .title {
        flex: 1;
      }
    }
  }

  .content {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    padding-bottom: 30px;

    .input {
      textarea {
        padding: 20px;
      }
    }

    .footer {
      display: flex;
      flex-direction: row;

      .assign {
        display: flex;
        flex-direction: row;
        align-items: center;
        width: 60%;

        .to {
          width: 100px;
        }
      }

      .action {
        width: 40%;
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;

        > * {
          height: 40px;
          margin-left: 10px;
        }
      }
    }
  }
}

