.root {
  .header-container {
    padding: 0;
    margin: 0;

    .header {
      display: flex;
      align-items: center;
      padding: 10px;

      .title {
        flex: 1;
      }
    }
  }

  .content {
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow: hidden;

    .basic-info {
      display: flex;
      padding-bottom: 20px;

      .label {
        flex: 1;
      }
    }
    .collapsible-cell {
      position: relative;
      margin: 0;
      padding: 0;

      .container {
        height: 0;
        overflow: hidden;
        transition: height 0.1s linear;
        background-color: #f1f1f1;
        border: 1px solid green;

        &.show-cell {
          height: unset;
        }

        .cell {
          padding: 19px;
          display: flex;
          flex-direction: column;

          .groups {
            display: flex;
            flex-direction: row;

            .input {
              width: 25%;
              padding: 10px;
            }
          }

          .reasons-container {
            display: table;

            .header {
              display: table-row;

              .label {
                display: table-cell;
                padding-right: 10px;
              }

              .delete {
                display: table-cell;
              }
            }

            .reasons {
              display: table-row;

              .input {
                display: table-cell;
                padding-right: 10px;
                width: 50%;
                padding-bottom: 15px;
              }

              .delete {
                display: table-cell;
                margin-top: 2px;
                text-align: center;
                vertical-align: middle;
              }
            }

            span.error {
              color: red;
            }

            .add-link {
              margin-top: 10px;
              font-weight: 500;
              text-decoration: underline;
              cursor: pointer;
              margin-left: 15px;

              &.empty-reasons {
                margin-top: 20px;
              }
            }
          }
        }
      }
    }

    .footer {
      display: flex;
      justify-content: flex-end;
      margin-top: 10px;

      button {
        margin: 5px;
      }
    }
  }
}
