.root {
  display: flex;
  flex-direction: column;
  padding-right: 10px;

  .contract-id {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
    word-break: break-all;
    .label {
      font-size: 14px;
    }
    .id {
      font-size: 16px;
      font-weight: bold;
    }
  }

  .plan-types {
    .node {
      color: black;
    }

    .benefit-label {
      color: darkslategray;
      .node {
        color: black;
      }
    }
  }
}
