.root {
  background-color: #efefef;
  padding: 5px;
  height: 250px;
  transition: padding 0.15s ease-in-out, background-color 0.15s linear;

  &.dragged {
    padding: 15px;
    background-color: white;
  }
  .container {
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #5d5d5d;
    border: dashed 2px #5f5f5f;

    &.clickable {
      cursor: pointer;
    }

    .icon {
      font-size: 25px;
      margin-bottom: 5px;
    }
    .label {
      font-weight: unset !important;
    }

    .warning {
      text-align: center;
      margin-top: 10px;

      span {
        font-weight: bold;
      }
    }

    .progress-container {
      width: 300px;
    }
  }
}
