.root {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 2px;

  .teeth {
    &.flip-x {
      transform: scaleX(-1);
    }

    &.flip-y {
      transform: scaleY(-1);
    }

    &.flip-x.flip-y {
      transform: scale(-1, -1);
    }

    // States

    // Default{
    .crown {
      fill: white;
      stroke: black;
      stroke-width: 2px;
    }

    .gums {
      fill: #ffa2a2;
      stroke: black;
      stroke-width: 2px;
    }

    // Extracted
    &.extracted {
      .crown {
        stroke: red;
        stroke-dasharray: 10;
        stroke-width: 4px;
        fill-opacity: 0;
      }
      .gums {
        opacity: 0;
      }
    }

    // Filled
    &.filled {
      .crown {
        fill: #929292;
      }
    }
  }

  .other-details {
    display: flex;
    flex-direction: column;
    align-items: center;

    .circle {
      min-height: 30px;
      min-width: 30px;
      /* width: 100%; */
      border-radius: 50%;
      border: solid 2px #5d5d5d;
    }
  }
}
