@import 'styles/color.scss';

.root {
  .container {
    padding-top:1rem;
    &:not(:last-child) {
      margin-bottom: 1.5rem;
    }

    .wrapper {
      padding: 1rem;

      .input {
        .MuiPaper-root {
          margin-bottom: unset;
        }
      }
    }
  }
}

.action {
  width: 18rem;
  position: absolute;
  right: 2rem;
  margin-bottom: 5;
  .typo {
    color: #2e7d32;
    text-decoration: underline;
  }
  .disabled {
    color: rgba(0, 0, 0, 0.5);
  }
}

.tally-sheet-dialog {
  .actions {
    padding-left: 0;
    padding-right: 0;
  }
}
