.root {
  width: 100%;
  display: flex;
  position: relative;
  flex-direction: column;

  .label {
    padding-bottom: 5px;
  }

  .wrapper {
    .field {
      flex: 1;
      display: flex;
      margin-bottom: 23px;
      flex-direction: column;
      border: 1px solid transparent;

      .form-control {
        width: 100%;
      }

      .input {
        width: 100%;
        padding-right: 5px;

        input {
          padding: 10px;
        }
      }

      &.focus {
        border-color: green;
      }

      // Errors
      &.has-error {
        border-color: red;
        margin-bottom: unset;
      }

      &.no-margin {
        margin-bottom: unset;
      }
    }
  }

  span.error {
    padding-top: 1px;
    color: red;
  }
}
