@import "styles/color.scss";

.root {
  display: flex;
  flex-direction: column;

  .header {
    margin-bottom: 20px;
  }

  .body {
    display: flex;
    flex-direction: column;
    overflow: hidden;

    .search {
      margin: 10px 1px;
    }

    .plan-types {
      display: flex;
      flex-direction: row;
      .type {
        cursor: pointer;
        margin-right: 10px;
      }
    }

    .table {
      flex: 1;

      .hidden {
        display: none;
      }

      th {
        padding: 5px;
        font-size: small;

        div {
          div {
            margin-bottom: unset !important;
          }
        }

        .search {
          width: 100%;
          font-size: unset;
          font-weight: normal;
          padding: 5px;
        }
      }

      td {
        vertical-align: unset;
        font-size: small;
        padding: 5px;
      }

      .cell {
        display: flex;
        flex-direction: column;
      }

      .handling {
        text-align: center;
        font-style: italic;
      }
    }
  }
}

.center {
  text-align: center;
  font-style: italic;
}
