@import 'styles/color.scss';

.root {
  display: flex;
  flex-direction: column;
  padding: 15px;
  white-space: nowrap;

  .table {
    display: table;

    .header {
      display: table-row;
      h6 {
        display: table-cell;
      }
    }

    .pair {
      display: table-row;

      .source {
        display: table-cell;
        padding-right: 10px;
        input {
          padding: 10px;
        }
      }

      .target {
        display: table-cell;
        padding-right: 10px;
      }

      .action {
        display: table-cell;
        padding-left: 5px;
        vertical-align: top;
      }
    }
  }
}
