@import "styles/color.scss";

.root {
width: 1732px;
padding: 15px;

  .header {
    display: flex;
    flex-direction: column;
    .typo {
      color: #2e7d32;
      text-decoration: underline;
    }
    .disabled {
      color: rgba(0, 0, 0, 0.5);
    }

    .content {
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      .filter {
        display: flex;
        flex-direction: row;
        .input {
          display: flex;
          flex-direction: row;
          width: 30%;
          margin-right: 5px;
          p {
            margin-right: 5px;
            margin-left: 5px;
          }
        }
        .submit {
          margin-bottom: 21px;
          margin-left: 15px;
          height: 40px;
        }
      }
    }
    .search-header {
      width: 100%;
      display: table;
      margin-top: 5px;
      // row
      .search-header-child {
        display: table-row;

        .input {
            width: 350px;
          }
       
        // columns
        .column-label {
          display: table-cell;
          width: 13%;
        }

        .column-input {
          display: table-cell;
          width: 35%;
        }

        .column-label2 {
          display: table-cell;
          width: 50%;
          padding-left: 50px;
        }
      }
    }
  }

  .body {
    .table {
      th {
        padding: 5px;
        white-space: nowrap;
        .input {
          padding: 5px;
        }
      }
      td {
        white-space: pre;
        padding: 16px 5px;
      }
      .right {
        padding-right: 50px;
      }
      .handling {
        padding: 2px;
        text-align: center;
        font-style: italic;
      }
    }
  }

  .table-row:nth-child(even) {
    background: #ccc;
  }
  .table-row:nth-child(odd) {
    background: #fff;
  }
  .table-row:hover {
    background: #ccf;
  }
}
