@import 'styles/color.scss';

.root {
  display: flex;
  flex-direction: column;

  .header {
    display: flex;
    flex-direction: column;
    position: relative;
    margin-top: 1rem;
    margin-bottom: 1rem;

    .title {
      margin-bottom: 1rem;
    }

    .dropdownWrapper {
      align-items: center;
      flex-direction: row;
      gap: 8px;

      .dropdownLabel {
        flex: 1;
      }

      .dropdown {
        flex: 4;
      }
    }

    .tabs {
      display: flex;

      .tab {
        padding: 1rem 1rem 0;
        cursor: pointer;

        &.selected {
          font-weight: bold;
          color: #3f51b5 !important;
        }
      }
    }

    .search {
      margin-top: 10px;
      margin-bottom: 10px;
    }
  }

  .table {
    .hidden {
      display: none;
    }

    th {
      padding: 5px;
      font-size: small;

      div {
        div {
          margin-bottom: unset !important;
        }
      }

      .search {
        width: 100%;
        font-size: unset;
        font-weight: normal;
        padding: 5px;
      }
    }

    td {
      vertical-align: unset;
      font-size: small;
      padding: 5px;
    }

    .cell {
      display: flex;
      flex-direction: column;
    }
  }
}

.doctors {
  .header-container {
    padding: 0;
    margin: 0;

    .header {
      display: flex;
      align-items: center;
      padding: 10px;

      .title {
        flex: 1;
      }
    }
  }
  .content {
    .table {
      th {
        padding: 10px;
        div {
          div {
            padding-left: 5px;
            padding-right: 5px;
            margin-bottom: unset !important;
          }
        }
      }
    }
  }
}

.center {
  text-align: center;
  font-style: italic;
}
