@import 'styles/color.scss';

.root {
  display: flex;
  flex-direction: column;
  padding: 15px;

  .header {
    display: flex;
    flex-direction: column;

    .disabled {
      color: rgba(0, 0, 0, 0.5);
    }

    .typo {
      color: #2e7d32;
      text-decoration: underline;
    }

    .input {
      display: flex;
      align-items: center;
      gap: 8px;

      .label {
        width: 40%;
      }
    }

    .field-filter {
      display: table;

      .input {
        display: table-row;
        flex-direction: row;
        white-space: nowrap;

        span {
          display: table-cell;
          padding-right: 5px;
        }
      }
    }
    .content {
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      .filter {
        display: flex;
        flex-direction: row;
        .input {
          display: flex;
          flex-direction: row;
          width: 30%;
          margin-right: 5px;
          p {
            margin-right: 5px;
            margin-left: 5px;
          }
        }
        .submit {
          margin-bottom: 21px;
          margin-left: 15px;
          height: 40px;
        }
      }
    }
  }

  .body {
    .table {
      max-height: 60vh;
      th {
        padding: 5px;
        white-space: nowrap;
        .input {
          padding: 5px;
        }
      }
      .remarks {
        min-width: 350px;
      }
      .table-cell {
        white-space: pre;
      }

      .handling {
        padding: 2px;
        text-align: center;
        font-style: italic;
      }
    }
    // TODO footer
    .footer {
      margin-top: 10px;
      display: flex;
      .label {
        flex: 1;
      }
      .total15Days {
        flex: 2;
        text-align: center;
      }
      .total1Week {
        flex: 2;
        text-align: center;
      }
      .total1Month {
        flex: 2;
        text-align: center;
      }
      .totalAll {
        flex: 2;
        text-align: center;
        padding-left: 10px;
      }
      .total15Days > * {
        padding-right: 85px;
      }
      .total1Week > * {
        padding-right: 108px;
      }
      .total1Month > * {
        padding-right: 53px;
      }
      .totalAll > * {
        padding-right: 30px;
      }
    }
  }

  .table-row:nth-child(even) {
    background: #ccc;
  }
  .table-row:nth-child(odd) {
    background: #fff;
  }
  .table-row:hover {
    background: #ccf;
  }
}
