.root {
  display: flex;
  width: 100%;
  flex-direction: column;

  .given-diagnosis{
    flex: 1;
    padding-left: 44px;
    padding-right: 201px;
    padding-bottom: 10px;
  }

  .header {
    display: flex;
    flex: 1;
    padding-bottom: 5px;

    .delete {
      width: 35px;
      text-align: left;
    }

    .description {
      padding-left: 10px;
      flex: 1;
    }

    .code {
      padding-left: 10px;
      width: 100px;
    }

    .primary {
      padding-left: 10px;
      width: 70px;
      text-align: center;
    }

    .discrepancy {
      width: 15px;
      text-align: left;
    }
  }

  .wrapper {
    flex: 1;

    &:last-child {
      padding-bottom: unset;
    }

    .sub-wrapper {
      display: flex;
      align-items: center;
      padding-bottom: 25px;

      .delete {
        width: 35px;
        text-align: left;
        margin-right: 10px;
      }
  
      .description {
        flex: 1;
      }
  
      .code {
        width: 90px;
        margin: 0 10px;
      }
  
      .primary {
        width: 60px;
        text-align: center;
      }
    }
  }
}
