@import "styles/color.scss";

.root {
  display: flex;
  flex-direction: column;
  padding: 15px;

  .header {
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;

    .content {
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      .filter {
        display: flex;
        flex-direction: row;

        .auto {
          width: 240px;
          margin-bottom: -30px;
          margin-top: 5px;
        }


        .dropdown {
          width: 160px;
          margin-right: 10px;
          margin-bottom: -20px;
        }

        .input {
          display: flex;
          flex-direction: row;
          width: 30%;
          margin-right: 5px;
          p {
            margin-right: 5px;
            margin-left: 5px;
          }
        }
        .submit {
          margin-bottom: 21px;
        }
        .radio-group {
          display: flex;
          flex-flow: row;
          width: fit-content;
          div.radio-container {
            display: flex;
            flex-flow: row;

            p:first-child {
              margin: 0;
              display: flex;
              flex-flow: column;
              justify-content: center;
              white-space: nowrap;
            }
          }
        }
      }
    }
  }

  .body {
    .chart {
      height: 479px;
    }
    .table {
      th {
        padding: 5px;
        white-space: nowrap;
        .input {
          padding: 5px;
        }
      }
      td {
        white-space: pre;
      }
    }

    // Horizontal ScrollBar
    div.graph-reports::-webkit-scrollbar {
      height: 8px;
      border: 1px solid black;
    }

    div.graph-reports::-webkit-scrollbar-track {
      background: #f1f1f1;
    }

    div.graph-reports::-webkit-scrollbar-thumb {
      background: #888;
    }

    div.graph-reports::-webkit-scrollbar-thumb:hover {
      background: #555;
    }

    div.graph-legend {
      height: fit-content;
      width: 100%;
      overflow: hidden;
      margin-bottom: 15px;

      div.legend-wrapper {
        display: flex;
        float: right;
        flex-flow: row;
        width: 60%;
        flex-wrap: wrap;

        div.legend-group {
          display: flex;
          flex-grow: 1;
          width: 33%;
          flex-flow: row;
          align-items: baseline;

          div.legend {
            height: 12px;
            width: 12px;
            margin-right: 5px;
          }
        }
      }
    }

    div.header-report {
      div.report-primary {
        width: 30%;
        display: inline-block;
        vertical-align: top;

        div.title {
          color: rgba(0, 0, 0, 0.5);
        }
      }

      .line-graph {
        display: inline-block;
        flex-direction: column;
        align-items: flex-end;
        flex: 1;
        width: 70%;

        .container {
          width: 100%;
          padding-left: 20px;

          .line-container {
            display: flex;

            .item {
              min-height: 5px;
            }
          }

          .line-details {
            display: flex;
            margin-top: 10px;

            .item {
              display: flex;
              flex: 1;
              flex-direction: column;

              .label {
                display: flex;

                .color {
                  height: 15px;
                  width: 15px;
                  margin-right: 10px;
                }
              }
            }
          }
        }
      }
    }

    div.graph-reports {
      height: 300px;
      width: 100%;
      white-space: nowrap;
      overflow-x: auto;
      padding-bottom: 15px;
      margin-bottom: 20px;

      div.graph-plot {
        display: inline-flex;
        width: fit-content;
        height: 100%;
        float: left;
        flex-flow: column;
        justify-content: space-between;
        text-align: right;
        border-right: solid 1px rgba(0, 0, 0, 0.5);
        padding-right: 10px;
        position: sticky;
        left: 0;
        background: white;
        z-index: 1;

        div.plot-items {
          display: flex;
          flex-flow: column;
          flex: 1;
          margin: 0;
          justify-content: flex-end;
        }

        div.plot-items:first-child {
          justify-content: end;
        }
      }
      div.graph-group {
        height: 100%;
        display: inline-flex;
        flex-flow: column;
        width: fit-content;

        div.graph-container {
          position: relative;
          // Default width for graphbar container
          width: 400px;
          height: 100%;
          border-radius: 6px;
          float: left;
          margin-left: 10px;

          div.bar-chart-wrapper {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 100%;
            height: 100%;
            padding: 0 5px;

            background: transparent;
            display: flex;

            div.bar-chart:nth-child(5) ~ div.bar-chart > div.graph + div.tooltip {
              right: 0;
            }

            div.bar-chart {
              position: relative;
              text-align: center;
              flex: 1;
              margin: 0 3px;

              div.graph + div.tooltip {
                display: none;
                position: absolute;
                bottom: 0;
                border: solid 1px rgba(0, 0, 0, 0.1);
                border-radius: 4px;
                padding: 10px;
                background: rgba(0, 0, 0, 0.5);
                color: #fff;
                z-index: 1;
              }

              div.tooltip:hover {
                display: block;
              }

              div.graph {
                position: absolute;
                width: 100%;
                bottom: 0;
                left: 50%;
                transform: translateX(-50%);
                cursor: pointer;
              }

              div.graph:hover + div.tooltip {
                display: block;
              }

              div.graph:before {
                content: "";
                position: absolute;
                top: 2px;
                left: 2px;
                right: 2px;
                bottom: 0;
                background: inherit;
              }
              // TODO
              /*
            * Temporary disabled
            * -Used to highlight bar color
            */
              // div.graph:after {
              //   content: "";
              //   position: absolute;
              //   top: 0;
              //   left: 0;
              //   width: 50%;
              //   height: 100%;
              //   background: rgba(255, 255, 255, 0.1);
              // }
            }
          }
        }
      }

      div.graph-category {
        float: left;
        width: 100%;
        text-align: center;
      }
    }

    .footer {
      display: flex;
      
      .label { 
        flex: 1;  
      }
    }
  }

  .table-row:nth-child(even) {
    background: #ccc;
  }
  .table-row:nth-child(odd) {
    background: #fff;
  }
  .table-row:hover {
    background: #ccf;
  }

  table.memberinfo {
    border: none;
  }
  table.memberinfo tr, table.memberinfo th, table.memberinfo td {
    border: none;
  }
  table.memberinfo tr:nth-child(even) {
    background: #fff;
  }
  table.memberinfo tr:nth-child(odd) {
    background: #fff;
  }
  table.memberinfo th, table.memberinfo td {
    text-align: left;
  }
  td.memberInfoLabel {
    width: fit-content;
    text-align: left;
    font-weight: normal;
    // border: 1px solid black !important;
  }
  td.memberInfoValue {
    text-align: left;
    white-space: nowrap;
    padding-right: 50px;
    font-weight: bold;
  }

  /*
  div.memberInfo {
    display: table;
  }
  div.memberInfoRow {
    display: table-row;
    width: 100%;
  }
  div.memberInfoLabel {
    display: table-cell;
    width: 100px;
    text-align: left;
    border: 1px solid black;
  }
  div.memberInfoValue {
    display: table-cell;
    text-align: left;
    min-width: 100px;
  }
  */

  hr {
    margin-bottom: 15px;
    border-top: solid 1px black;
    width: 100%;
  }
}
