@import 'styles/color.scss';

.root {
  flex: 1;
  display: flex;
  flex-direction: column;

  .search {
    margin-bottom: 10px;
  }

  .table {
    flex: 1;
    height: 300px;

    th {
      padding: 5px;
      // white-space: nowrap;
      font-size: small;
      .input {
        padding: 5px;
      }
    }
    td {
      white-space: pre;
      font-size: small;
    }

    tr {
      &.selected {
        background-color: $color-10 !important;
      }

      &.default-override {
        background-color: white !important;
      }
    }
  }

  .footer {
    display: flex;
    justify-content: flex-end;

    button {
      margin: 5px;
    }
  }
}
