.root {
  display: flex;
  flex-direction: column;
  flex: 1;

  .content {
    display: flex;
    flex-direction: column;
    padding: 15px;
    flex: 1;

    .first-container {
      margin-bottom: 10px;
      display: flex;

      .action {
        align-items: center;
        display: flex;
        margin-right: 10px;
      }

      .search {
        flex: 1;
      }
    }

    .table {
      flex: 1;
      display: flex;
      flex-direction: column;
      // margin-bottom: 40px;
    }

    .footer {
      display: flex;
      padding-top: 20px;

      .actions {
        display: flex;
        flex: 1;
        justify-content: flex-end;

        button:last-child {
          margin-left: 10px;
        }
      }
    }
  }
}
