.root {
  .header-container {
    padding: 0;
    margin: 0;

    .header {
      display: flex;
      align-items: center;
      padding: 10px;

      .title {
        flex: 1;
      }
    }
  }

  .content {
    display: flex;
    flex-direction: column;
    text-align: center;
    overflow-y: hidden;
    background-color: rgb(232, 232, 232);

    div[class*='viewer'],
    div[class*='unsupported-message'] {
      flex: 1;
      width: unset !important;
      height: unset !important;
    }

    img {
      width: 80% !important;
      height: unset !important;
    }
  }
}
