@import 'styles/color.scss';

.root {
  display: flex;
  flex-direction: column;
  padding: 15px;

  .section {
    margin-bottom: 10px;

    .groups {
      display: flex;
      flex-direction: row;

      .input {
        width: 25%;
        padding: 10px;
      }

      .field {
        display: flex;
        flex-direction: row;
        width: 100%;
        padding-left: 50px;

        .label {
          width: 20%;
        }
        .table {
          width: 50%;
        }
      }
    }
  }
}

.dialog {
  .header-container {
    padding: 0;
    margin: 0;

    .header {
      display: flex;
      align-items: center;
      padding: 10px;

      .title {
        flex: 1;
      }
    }
  }
  .content {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    padding-bottom: 30px;

    .input {
      input {
        padding: 10px;
      }
    }

    .footer {
      display: flex;
      flex-direction: row;
      .action {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        > * {
          height: 40px;
          margin-left: 10px;
        }
      }
    }
  }
}
