.root {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 100%;
  width: 100%;
  position: relative;

  .container {
    display: flex;
    height: 40px;
    width: 100%;
    cursor: pointer;
    border-radius: 5px;
    outline: none !important;
    position: relative;
    border: 1px solid transparent;
    margin-top: 3px;

    &.focus {
      border-color: green;
    }

    // Errors
    &.has-error {
      border-color: red;
      margin-bottom: unset;
    }

    .label {
      border-radius: 5px;
      background-color: green;
      color: white;
      padding: 0 10px;
      margin: 1px;
      display: flex;
      align-items: center;
    }

    .values {
      flex: 1;
      align-items: center;
      display: flex;
      padding: 0 10px;

      span {
        font-weight: 500;
      }
    }

    .suggestions {
      z-index: 2;
      position: absolute;
      margin-top: 40px;
      min-width: 100%;
      border-radius: 0 0 5px 5px;
      display: flex;
      flex-direction: column;
      cursor: default;
      padding-bottom: 20px;

      &.add-margin {
        margin-top: 65px;
      }

      .header {
        padding: 10px;
        border-bottom: 1px solid #afafaf;
      }

      .values-container {
        .message {
          padding: 20px;
          text-align: center;

          p {
            font-weight: 500;
          }
        }

        .list {
          flex: 1 1;
          align-items: center;
          display: flex;
          padding: 0 5px;

          .item {
            width: 50%;
            text-align: center;
            padding: 5px;
            border-radius: 5px;
            margin: 5px;
            color: white;
            background-color: green;
            cursor: pointer;
            font-size: 13px;
          }
        }
      }
    }
  }

  span.error {
    position: absolute;
    padding-top: 42px;
    color: red;
  }
}
