@import "styles/color.scss";

.footer {
  .total {
    display: table;
    width: 500px;
    float: right;
    .total-row {
      display: table-row;

      .total-cell {
        display: table-cell;
        padding-right: 15px;
        // width: 200px;
        text-align: right;
      }
    }
  }

  .all-signatories {
    margin-top: 45px;
    .signatories {
      padding: 5px;
      display: flex;

      .username {
        margin-left: 10px;
      }

      .line-note {
        width: 200px;
        border-bottom: 1px solid black;
        margin-left: 31px;
      }

      .line-received {
        width: 200px;
        border-bottom: 1px solid black;
        margin-left: 10px;
      }
    }
  }
}
